<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Cargando contenido...</span>
        </div>
    </div>
   <DeptoBase v-else
    :goal="preexpansion.meta_semana"
    :progress="preexpansion.progress"
    :name="preexpansion.nombre"
    :depto="preexpansion.depto"
    :days="preexpansion.dias_laborados"
    :m3="preexpansion.m3_persona"
    :bonus_p="preexpansion.bono_persona"
    :bonus_tot="preexpansion.bono_depto"
    :asis="preexpansion.asistencia"
    :extra="preexpansion.$_extra_m3"
    :bonus_prod="preexpansion.bono_productividad"
    :bonus_goals="preexpansion.bono_metas"
    />
</div>
</template>

<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Preexpansion',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'preexpansion',
            userCode: this.$route.params.user,
            city: 'nogales'
        }); 
    },
    computed: {
        isLoading() {            
            return this.$store.getters["plants/loading"];
        },
        preexpansion() {            
            return this.$store.getters["plants/data"];
        }
    }
}
</script>